  background-color: transparent;
  background-image: url(/static/media/button_close.4c6a2169.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 32px;
  transition: opacity 0.3s linear;
  cursor: pointer;
  border: 0;
