  width: 282px;
  background: #ffffff;
  border-radius: 10px;
  position: relative;
}

.elements__photo {
  width: 282px;
  height: 282px;
  background: #c4c4c4;
  border-radius: 10px 10px 0px 0px;
  display: block;
  object-fit: cover;
  opacity: 1;
  transition: opacity 0.3s linear;
}

.elements__photo:hover {
  cursor: pointer;
  opacity: 0.6;
}

.elements__wrapper {
  display: flex;
  margin: 25px 15px 26px 21px;
  justify-content: space-between;
  align-items: center;
}

.elements__title {
  max-width: 218px;
  font-family: "Inter", Arial, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin: 0;
  margin-right: auto;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.elements__like-button {
  width: 22px;
  height: 19px;
  display: block;
  background-color: transparent;
  background-image: url(/static/media/white_like.cb5f1643.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 22px;
  transition: opacity 0.3s linear;
  cursor: pointer;
  border: none;
  padding: 0;
  margin: 0;
}

.elements__like-button:hover {
  opacity: 0.5;
}

.elements__like-button_active {
  background-image: url(/static/media/black_like.933106ea.svg);
}

.elements__trash-button {
  position: absolute;
  top: 20px;
  left: 244px;
  width: 18px;
  height: 19px;
  display: block;
  background-color: transparent;
  background-image: url(/static/media/trash_button.48bb334e.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 18px;
  transition: opacity 0.3s linear;
  cursor: pointer;
  border: none;
  padding: 0;
  margin: 0;
  z-index: 1;
}

.elements__trash-button:hover {
  opacity: 0.6;
}

.elements__trash-button_hidden {
 visibility: hidden;
}

.elements__like-zone-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}


.elements__likes-number {
  width: 10px;
  height: 13px;

  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  text-align: center;

  margin: 0;
  padding-top: 3px;

  color: #000000;
}

